import Mock from 'mockjs'
import user from './modules/user'
import vehicle from './modules/individual-verhicle'
import budget from './modules/budget'
import manageInspect from './modules/manageInspect'
import agency from './modules/agency'

import {generateUUID} from 'utils/uuid'

// 
Mock.mock('/api/initData', 'get', vehicle.formInit)

// 
Mock.mock('/api/sectorlist', 'get', vehicle.sectorList)

// 
Mock.mock('/api/querySector', 'get', vehicle.sectorData)

// 
Mock.mock('/api/getDimensionBySector', 'get', vehicle.sectorDimension)

// 
Mock.mock('/api/recommend', 'get', vehicle.recommend)

// 
Mock.mock('/api/customDimension', 'get', vehicle.baseRecommend)

// 
Mock.mock('/api/lifeTable', 'get', vehicle.lifeTable)

// 
Mock.mock('/api/inspect/initData', 'get', manageInspect.formData)
Mock.mock('/api/inspect/initPersonData', 'get', manageInspect.personFormData)
Mock.mock('/api/inspect/initRealFormData', 'get', manageInspect.initRealFormData)

// 
Mock.mock('/api/budget/initData', 'get', budget.formData)

// 
Mock.mock('/api/agency/initConfigData', 'get', agency.configFormData)

// 
Mock.mock('/api/login', 'post', options => {
  const user = JSON.parse(options.body)
  if (user.username === 'admin' && user.password === 'chexianbaojia') {
    return {
      result: true,
      token: generateUUID()
    }
  } else {
    return {
      result: false
    }
  }
})

// 
Mock.mock('/api/getInfo', 'get', user.userInfo)

export default Mock



