import request from 'utils/request'

// 
export const initData = () => {
  return request({
    url: '/api/initData',
    method: 'get'
  })
}

export const sectorList = () => {
  return request({
    url: '/api/sectorlist',
    method: 'get'
  })
}

// 
export const querySector = (param) => {
  return request({
    url: '/api/querySector',
    method: 'get',
    param
  })
}

// 
export const recommend = (param) => {
  return request({
    url: '/api/recommend',
    method: 'get',
    param
  })
}

// 
export const getDimensionBySector = (param) => {
  return request({
    url: '/api/getDimensionBySector',
    method: 'get',
    param
  })
}

// 
export const customDimension = (param) => {
  return request({
    url: '/api/customDimension',
    method: 'get',
    param
  })
}

export const querySectorInfo = (id) => {
  return request({
    url: '',
    method: 'get',
    params: {id}
  })
}

// 
export const lifeTable = (param) => {
  return request({
    url: '/api/lifeTable',
    method: 'get',
    param
  })
}



